
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f0f0f0;
  margin-top: 50px;
  color: #444444;
}
#app a,
  #app a:link,
  #app a:active,
  #app a:visited {
    color: #444444;
}
.center.aligned {
  text-align: center;
}
.left.aligned {
  text-align: left;
}
.right.aligned {
  text-align: right;
}
.appbar {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: #e7e7e7;
  z-index: 100;
}
.appbar .logo {
    float: left;
    padding: 10px;
}
.appbar .right {
    float: right;
    padding: 10px;
}
.flat-button {
  -webkit-appearance: none;
  border: none;
  padding: 10px;
  background-color: transparent;
  -webkit-transition: background-color 0.6s;
  transition: background-color 0.6s;
}
.flat-button:hover {
    cursor: pointer;
    background-color: #ddd;
}
.flat-button.active {
    background-color: #e7e7e7;
    color: white;
}
.raised-button {
  border: none;
  padding: 10px;
  background-color: #e7e7e7;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
  cursor: pointer;
}
.raised-button:hover {
    background-color: #e0e0e0;
}
.raised-button.active {
    background-color: #DF0101;
    color: white;
}
.input {
  -webkit-appereance: none;
}
.input input,
  .input select {
    border: none;
    border-bottom: 1px solid #d7d7d7;
    font-size: 1em;
    line-height: 2em;
    padding: 10px;
    background-color: transparent;
    -webkit-transition: background-color 0.5s;
    transition: background-color 0.5s;
}
.input input:hover,
    .input select:hover {
      cursor: pointer;
      background-color: #e0e0e0;
}
.input textarea {
    border: 1px solid #d7d7d7;
    line-height: 2em;
    padding: 10px;
    background-color: transparent;
    -webkit-transition: background-color 0.5s;
    transition: background-color 0.5s;
}
.input textarea:hover {
      cursor: pointer;
      background-color: #e0e0e0;
}
.input textarea::-webkit-input-placeholder {
      font-family: Helvetica, sans-serif;
      font-size: 1.3em;
      text-align: center;
      padding-top: 40px;
}
.menu {
  width: 100%;
  z-index: 100;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  background-color: #fcfcfc;
}
.menu .item {
    padding: 10px;
    -webkit-transition: background-color 0.6s;
    transition: background-color 0.6s;
    display: block;
    font-size: 0.8em;
}
.menu .item:hover {
      cursor: pointer;
      background-color: #e0e0e0;
}
.menu .item.active {
      background-color: #e0e0e0;
}
.divider {
  border-bottom: 1px dashed #d7d7d7;
  margin: 0 12px;
}
.spacer {
  height: 10px;
}
@media screen and (max-width: 1002px) {
.container {
    width: 100%;
    margin: 0;
}
.mobile-100 {
    width: calc(100% - 20px);
    margin: 10px;
}
.list .image {
    width: calc(100% - 30px) !important;
    padding-bottom: 10px;
}
.list .details {
    width: calc(100% - 20px) !important;
}
.list .details .brief {
      -webkit-line-clamp: 3 !important;
}
}
@media screen and (min-width: 1002px) {
.container {
    width: 1100px;
    margin: auto;
}
.desktop-center {
    float: left;
    width: calc(60% - 10px);
    margin: 5px;
}
.desktop-right {
    float: left;
    width: calc(27% - 10px);
    margin: 5px;
}
.desktop-left {
    float: left;
    width: calc(13% - 10px);
    margin: 5px;
}
}
.list {
  width: 100%;
  margin-bottom: 5px;
  padding: 15px 0;
  display: inline-block;
  background-color: #fcfcfc;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
}
.list:hover {
    background-color: #e0e0e0;
    cursor: pointer;
}
.list .image {
    position: relative;
    width: 25%;
    float: left;
    padding-left: 15px;
}
.list .image img {
      width: 100%;
}
.list .details {
    float: left;
    margin-left: 10px;
    width: calc(100% - 25% - 30px);
}
.list .details .name {
      width: calc(100% - 10px);
      font-size: 0.8em;
      font-weight: bold;
}
.list .details .meta {
      font-size: 0.75em;
      line-height: 2.5em;
      color: #999999;
}
.list .details .meta div {
        display: inline;
}
.list .details .brief {
      font-size: 0.8em;
      line-height: 1.7em;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
}
.list:after {
    content: " ";
    clear: both;
}
.recommend {
  background-color: #fcfcfc;
}
.recommend .title {
    font-weight: bold;
    font-size: 0.9em;
    padding: 10px;
    color: #777;
}
.recommend .item {
    padding: 10px;
    -webkit-transition: background-color 0.5s;
    transition: background-color 0.5s;
    display: block;
}
.recommend .item:hover {
      background-color: #e0e0e0;
      cursor: pointer;
}
.recommend .item .index {
      padding: 3px 7px;
      margin-right: 10px;
      font-size: 0.7em;
      background-color: #e7e7e7;
}
.recommend .item .name {
      font-size: 0.8em;
}
.keywords {
  background-color: #fcfcfc;
  margin: 0 auto;
}
.keywords .title {
    font-weight: bold;
    font-size: 0.9em;
    padding: 10px;
    color: #777;
}
.keywords .item {
    display: inline-block;
    padding: 10px;
    background-color: #f0f0f0;
    -webkit-transition: background-color 0.5s;
    transition: background-color 0.5s;
    font-size: 0.8em;
    margin-left: 5px;
    margin-top: 5px;
}
.keywords .item:last-child {
      margin-bottom: 5px;
}
.keywords .item:hover {
      background-color: #e0e0e0;
      cursor: pointer;
}
.keywords .item.active {
      background-color: #e7e7e7;
}
.paper {
  background-color: #fcfcfc;
  padding: 10px;
  margin-bottom: 10px;
}



